"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';

interface Message {
  id: string;
  text: string;
  isUser: boolean;
  timestamp: Date;
}

const ChatWidget: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      text: "Hi! I'm your personal style advisor. I can help you with outfit suggestions, styling tips, and fashion advice. What would you like to know?",
      isUser: false,
      timestamp: new Date()
    }
  ]);
  const [inputText, setInputText] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const getStyleAdvice = (userMessage: string): string => {
    const lowerMessage = userMessage.toLowerCase();
    
    // Style advice based on keywords
    if (lowerMessage.includes('dress') || lowerMessage.includes('formal')) {
      return "For formal occasions, I recommend our elegant midi dresses or tailored blazers. Pair them with classic accessories like our leather ankle boots and a structured bag for a polished look.";
    }
    
    if (lowerMessage.includes('casual') || lowerMessage.includes('everyday')) {
      return "For casual everyday wear, try our cotton t-shirts with high-waisted jeans and a denim jacket. Add our crossbody bag for a comfortable yet stylish look that's perfect for daily activities.";
    }
    
    if (lowerMessage.includes('work') || lowerMessage.includes('office')) {
      return "For professional settings, consider our tailored blazers with wide-leg trousers or a silk blouse. Our wool coats are perfect for commuting, and leather ankle boots complete the sophisticated look.";
    }
    
    if (lowerMessage.includes('winter') || lowerMessage.includes('cold')) {
      return "For colder weather, layer our cozy knit sweaters under our classic wool coat. Don't forget our cashmere scarf for extra warmth and elegance. Pair with our leather ankle boots for style and comfort.";
    }
    
    if (lowerMessage.includes('summer') || lowerMessage.includes('warm')) {
      return "For warm weather, our flowy midi dresses and cotton t-shirts are perfect. Pair them with comfortable sandals and our crossbody bag for a breezy, effortless summer look.";
    }
    
    if (lowerMessage.includes('color') || lowerMessage.includes('colours')) {
      return "This season, we're loving neutral tones like camel, cream, and grey for versatility. Black and navy are timeless classics, while a pop of color like blush or forest green can add personality to any outfit.";
    }
    
    if (lowerMessage.includes('size') || lowerMessage.includes('fit')) {
      return "Finding the right fit is crucial! Check our size guide for measurements. For a flattering silhouette, try our high-waisted jeans or wide-leg trousers. Our tailored blazers are designed to enhance your natural shape.";
    }
    
    if (lowerMessage.includes('accessory') || lowerMessage.includes('accessories')) {
      return "Accessories can transform any outfit! Our cashmere scarves add elegance, while our crossbody bags are both practical and stylish. Don't underestimate the power of the right shoes - our leather ankle boots go with everything.";
    }
    
    if (lowerMessage.includes('budget') || lowerMessage.includes('affordable')) {
      return "Great style doesn't have to break the bank! Start with our essential cotton t-shirts and build your wardrobe with versatile pieces like our classic denim jacket and high-waisted jeans. These basics mix and match beautifully.";
    }
    
    if (lowerMessage.includes('trend') || lowerMessage.includes('trendy')) {
      return "Current trends we're loving include oversized blazers, high-waisted everything, and sustainable fabrics. Our cozy knit sweaters and wide-leg trousers are perfectly on-trend while remaining timeless.";
    }
    
    // Default responses for general queries
    const defaultResponses = [
      "I'd be happy to help you with that! Based on our current collection, I recommend checking out our versatile pieces that can be mixed and matched for different occasions.",
      "That's a great question! Our styling philosophy focuses on timeless pieces that work for your lifestyle. What specific occasion or style are you looking for?",
      "Fashion is all about expressing your personality! Tell me more about your style preferences, and I can suggest some pieces from our collection that might work for you.",
      "I love helping with style advice! Our current collection has some amazing versatile pieces. Are you looking for something specific like workwear, casual outfits, or special occasion wear?"
    ];
    
    return defaultResponses[Math.floor(Math.random() * defaultResponses.length)];
  };

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputText,
      isUser: true,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputText('');
    setIsLoading(true);

    try {
      // Simulate thinking time
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const advice = getStyleAdvice(inputText);
      
      const botMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: advice,
        isUser: false,
        timestamp: new Date()
      };

      setMessages(prev => [...prev, botMessage]);
    } catch (error) {
      console.error('Error generating style advice:', error);
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: "I'm sorry, I'm having trouble right now. Please try asking your style question again!",
        isUser: false,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button 
          className="fixed bottom-6 right-6 rounded-full w-14 h-14 shadow-lg hover:shadow-xl transition-all duration-300"
          size="icon"
        >
          ðŸ’¬
        </Button>
      </DialogTrigger>
      
      <DialogContent className="sm:max-w-md h-[600px] flex flex-col">
        <DialogHeader>
          <DialogTitle>Style Advisor Chat</DialogTitle>
        </DialogHeader>
        
        <div className="flex-1 flex flex-col">
          {/* Messages */}
          <div className="flex-1 overflow-y-auto space-y-4 p-4 bg-secondary/10 rounded-lg">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}
              >
                <div
                  className={`max-w-[80%] p-3 rounded-lg ${
                    message.isUser
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-background border'
                  }`}
                >
                  <p className="text-sm">{message.text}</p>
                  <p className="text-xs opacity-70 mt-1">
                    {message.timestamp.toLocaleTimeString([], { 
                      hour: '2-digit', 
                      minute: '2-digit' 
                    })}
                  </p>
                </div>
              </div>
            ))}
            
            {isLoading && (
              <div className="flex justify-start">
                <div className="bg-background border p-3 rounded-lg">
                  <p className="text-sm">Thinking about your style...</p>
                </div>
              </div>
            )}
          </div>
          
          {/* Input */}
          <div className="flex gap-2 mt-4">
            <textarea
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Ask me about styling, trends, or outfit ideas..."
              className="flex-1 p-2 border rounded-md resize-none h-12"
              disabled={isLoading}
            />
            <Button 
              onClick={handleSendMessage}
              disabled={!inputText.trim() || isLoading}
              size="icon"
            >
              âž¤
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default ChatWidget;
